body {
    background-color: #f5deb3;
}

.title {
    margin-left: 60px;
    font-family: 'Macondo Swash Caps', cursive;
    font-size: 300%;
}

.outer-box {
    margin-right: 35px;
}

a:link,
a:visited {
    text-decoration: none;
    color: #000000;
}

li:hover {
    background-color: #fad591;
}

li {
    list-style-type: none;
    padding: 15px;
}

span {
    display: block;
    margin-left: 15px;
    color: #817676;
}


